.howWeKeep {
  margin-top: 60px;
}

.howWeKeep__title {
  @include textTitle();
  line-height: 36px;
  margin: 0;
  padding: 0;
  text-align: center;
  padding-left: 50px;
}

.howWeKeep__text {
  margin: 0;
  padding: 0;
  margin-top: 15px;
  @include textParagraph;
  text-align: center;
}

.howWeKeep__cards {
  display: flex;
  padding: 0;
  margin: 0;
  margin-top: 25px;
  list-style: none;
  justify-content: space-between;
}

.howWeKeep__item {
  max-width: 255px;
  min-height: 211px;
  background-color: #0c8543;
  border-radius: 8px;
  p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 400;
    font-size: 21px;
    line-height: 27px;
    text-align: center;
    color: white;
    padding: 0 30px;
    margin: 0;
    margin-top: 15px;
  }
}

.howWeKeep__itemImg {
  width: 100%;
  height: 126px;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}

@include media-breakpoint-down(xs) {
  .howWeKeep__cards {
    margin-top: 25px;
    list-style: none;
    justify-content: space-between;
  }

  .howWeKeep__item {
    max-width: 230px;
    min-height: 211px;
    p {
      font-size: 18px;
      line-height: 20px;
    }
  }

  .howWeKeep__itemImg {
    width: 100%;
    height: 120px;
  }
}

@include media-breakpoint-down(lg) {
  .howWeKeep {
    display: none;
  }
}
